'use strict'

import toast from '../../../plugins/toast.js'
import popupNhanVien from '../core/popupNhanVien.vue'
import popupPhanQuyen from '../core/popupPhanQuyen.vue'
import moment from 'moment'
export default {
  components: {
    popupNhanVien,
    popupPhanQuyen
  },
  data() {
    return {
      open: false,
      edit: false,
      disabled: true,
      isThem: false,
      NhanVienSelect: {},
      pagination: {},
      selected: [],
      headers: [
        { text: 'H Tn Nhn Vin',value: 'tenNhanVien' },
        { text: 'nh i Din', value: 'anhDaiDien' },
        { text: 'Ngy Sinh', value: 'ngaySinh' },
        { text: 'Gii Tnh', value: 'gioiTinh' },
        { text: 'S Chng Minh', value: 'soCMND' },
        { text: 'S in Thoi', value: 'soDienThoai' },
        { text: 'a Ch', value: 'diaChi' },
        { text: 'Chc v', value: 'ChucVu' },
        { text: 'Thao Tc', value: '' }
      ],
      loading: true,
      snackbar: false,
      snackbarMulti: false,
      idXoa: '',
      tenNhanVien: '',
      isPhanQuyen: false,
      openPhanQuyen: false
    }
  },
  created() {
    this.loading = false
    let user  = this.$store.get('auth/user')
    if(user.rolesGroupID.roles && user.rolesGroupID.roles.length > 0 && user.rolesGroupID.roles.some(item => String(item._id) === '5cc59aaada3e4e5b6c97390c')) {
      this.isPhanQuyen = true
    }
    this.$store.dispatch('nhanvien/getNhanViens').then(res => {
      if(res === false) {
        this.$router.push({path:'/404.html'})
      }
    })
  },
  computed: {
    pages () {
      if (this.pagination.rowsPerPage == null ||
        this.pagination.totalItems == null
      ) return 0
      return Math.ceil(this.dsNhanVien.length / this.pagination.rowsPerPage)
    },
    dsNhanVien () {
      return this.$store.state.nhanvien.dsNhanVien
    }
  },
  watch: {
    selected () {
      if(this.selected && this.selected.length == 0)
      {
        this.disabled = true
      }
      else
      {
        this.disabled = false
      }
    }
  },
  methods: {
    OpenPhanQuyen () {
      this.openPhanQuyen = true
    },
    toggleAll () {
      if (this.selected.length) 
      {
        this.selected = []
      }
      else
      {
        this.selected = this.dsNhanVien.slice()
      }
    },
    changeSort (column) {
      if (this.pagination.sortBy === column) {
        this.pagination.descending = !this.pagination.descending
      } else {
        this.pagination.sortBy = column
        this.pagination.descending = false
      }
    },
    OpenThem () {
      this.isThem = true
      this.edit = true
    },
    OpenTimKiem () {
      this.open = true
    },
    GotoEdit(item) {
     this.isThem = false
     this.edit = true
     this.NhanVienSelect = item
    },
    ResetPage () {
      this.$store.dispatch('nhanvien/getNhanViens')
    },
    OpenSnackback (khachthue) {
      this.tenNhanVien = khachthue.tenNhanVien
      this.idXoa = khachthue._id
      this.snackbar = true
    },
    Delete () {
      if(this.idXoa != '')
      {
        this.$store.dispatch('nhanvien/deleteNhanVien', this.idXoa).then( res => {
          toast.Success(' xa khch thu: '+ res.hoNhanVien + ' ' + res.tenNhanVien)
          this.snackbar = false
        }).catch( res => {
          toast.Error('C li xy ra: ' + res)
        })
      } else {
        toast.Error('Vui lng chn phng cn xa!')
      }
    },
    DeleteMulti () {
      if (this.selected && this.selected.length != 0)
      {
        let deleteMultiId = this.selected.map(item => item._id)
        this.$store.dispatch('nhanvien/deleteMultiNhanVien', deleteMultiId).then(() => {
          toast.Success('Xa thnh cng!')
          this.snackbarMulti = false
          this.disabled = true
        })
      } else {
        toast.Show('Vui lng chn phng  xa')
      }
    },
    OpenDeleteMulti () {
      this.snackbarMulti = true
    },
    ResetSnackback () {
      this.idXoa = '',
      this.tenNhanVien = '',
      this.snackbar = false
    },
    ResetSnackbackMulti () {
      this.snackbarMulti = false
    }
  },
  filters: {
    formarDate (date) {
      return moment(date).format('DD/MM/YYYY')
    }
  }
}