import DatePicker from 'vue2-datepicker'
import ThongKeServices from '../services.js'
import toast from '../../../plugins/toast'
import moment from 'moment'
import downloadExcel from 'vue-json-excel'
export default {
  components: { DatePicker, downloadExcel },
  data() {
    return {
      formData: {},
      search: '',
      nameExcel:'',
      pagination: {
        rowsPerPage:5
      },
      loadingTK:false,
      lang: {
        days: ['CN', 'T.2', 'T.3', 'T.4', 'T.5', 'T.6', 'T.7'],
        months: ['Thng 1', 'Thng 2', 'Thng 3', 'Thng 4', 'Thng 5', 'Thng 6', 'Thng 7', 'Thng 8', 'Thng 9', 'Thng 10', 'Thng 11', 'Thng 12'],
        pickers: ['ti 7 ngy', 'ti 1 thng', 'lui 7 ngy', 'lui 1 thng'],
        placeholder: {
          date: 'Select Date',
          dateRange: 'Chn ngy thng k'
        }
      },
      headers: [
        { text: 'M S Hp ng',value: '_id' },
        { text: 'H Tn Khch Thu', value: 'khacThueID.tenKhachThue' },
        { text: 'Tn Phng', value: 'phongID.tenPhong' },
        { text: 'Ngy Lp', value: 'ngayLap' },
        { text: 'Ngy Kt Thc', value: 'ngayKetThuc' }
      ],
      exportHeader: {
        'M Hp ng': '_id',
        'H Khch Thu': 'khachThueID.hoKhachThue',
        'Tn Khch Thu': 'khachThueID.tenKhachThue',
        'Phng Thu': 'phongID.tenPhong',
        'Khu Phng': 'phongID.khuPhongID.tenKhuPhong',
        'Ngy Kt Thc Hp ng': {
          field: 'ngayKetThuc',
          callback: (value) => {
            return  moment(value).format('DD/MM/YYYY')
          }
        } 
      },
      dsHD: []
    }
  },
  computed: {
    pages () {
      if (this.pagination.rowsPerPage == null ||
        this.dsHD.length  === 0
      ) return 0
      return Math.ceil(this.dsHD.length / this.pagination.rowsPerPage)
    }
  },
  methods: {
   async ThongKePhong () {
      try {
        this.loadingTK = true
        this.dsHD = await ThongKeServices.ThongKe(this.formData)
        let date = new Date()
        this.nameExcel = `hop-dong-het-han-${date.getMonth() +1 +'-' +date.getFullYear()}.xls`
        if(this.dsHD.length > 0)
        {
          this.loadingTK = false
          toast.Success(`C ${this.dsHD.length} ${this.formData.tieuChi === 'hdLap'? 'Hp ng Lp':'Hp ng Ht Hn'} c tm thy!`)
        } else {
          this.loadingTK = false
          toast.Info(`Khng c  ${this.formData.tieuChi === 'hdLap'? 'Hp ng Lp':'Hp ng Ht Hn'} no c tm thy!`)
        }
        
      } catch (err) {
        toast.Error(err)
      }
    },
    async BaoHetHan () {
      try {
        let res = await ThongKeServices.BaoHetHan(this.dsHD)
        if(res) {
          toast.Success(' gi mail bo ht hn hp ng!')
        }
      } catch (err) {
        toast.Error(err)
      }
    }
  },
  filters: {
    formarDate (date) {
      return moment(date).format('DD/MM/YYYY')
    }
  }
}