'use strict'

import toast from '../../../plugins/toast.js'
import popupKhachThue from '../core/popupKhachThue.vue'
import popupTimKiem from '../core/popupTimKiem.vue'
import moment from 'moment'
export default {
  components: {
    popupKhachThue,
    popupTimKiem
  },
  data() {
    return {
      open: false,
      edit: false,
      disabled: true,
      isThem: false,
      khachThueSelect: {},
      pagination: {},
      selected: [],
      headers: [
        { text: 'H Tn Khch',value: 'tenKhachThue' },
        { text: 'nh i Din', value: 'anhDaiDien' },
        { text: 'Ngy Sinh', value: 'ngaySinh' },
        { text: 'Gii Tnh', value: 'gioiTinh' },
        { text: 'S Chng Minh', value: 'soCMND' },
        { text: 'S in Thoi', value: 'soDienThoai' },
        { text: 'a Ch', value: 'diaChi', width: 247 },
        { text: 'Tnh Trng Khch', value: 'tinhTrangKhachThue' },
        { text: 'Thao Tc', value: '' }
      ],
      loading: true,
      snackbar: false,
      snackbarMulti: false,
      idXoa: '',
      tenKhachThue: '',   
    }
  },
  created() {
    this.loading = false
    this.$store.dispatch('getKhachs')
  },
  computed: {
    pages () {
      if (this.pagination.rowsPerPage == null ||
        this.pagination.totalItems == null
      ) return 0
      return Math.ceil(this.dsKhachThue.length / this.pagination.rowsPerPage)
    },
    dsKhachThue () {
      return this.$store.state.khachthue.dsKhachThue
    }
  },
  watch: {
    selected () {
      if(this.selected && this.selected.length == 0)
      {
        this.disabled = true
      }
      else
      {
        this.disabled = false
      }
    }
  },
  methods: {
    toggleAll () {
      if (this.selected.length) 
      {
        this.selected = []
      }
      else
      {
        this.selected = this.dsKhachThue.slice()
      }
    },
    changeSort (column) {
      if (this.pagination.sortBy === column) {
        this.pagination.descending = !this.pagination.descending
      } else {
        this.pagination.sortBy = column
        this.pagination.descending = false
      }
    },
    OpenThem () {
      this.isThem = true
      this.edit = true
    },
    OpenTimKiem () {
      this.open = true
    },
    GotoEdit(item) {
     this.isThem = false
     this.edit = true
     this.khachThueSelect = item
    },
    ResetPage () {
      this.$store.dispatch('getKhachs')
    },
    OpenSnackback (khachthue) {
      this.tenKhachThue = khachthue.tenKhachThue
      this.idXoa = khachthue._id
      this.snackbar = true
    },
    Delete () {
      if(this.idXoa != '')
      {
        this.$store.dispatch('deleteKhachThue', this.idXoa).then( res => {
          toast.Success(' xa khch thu: '+ res.hoKhachThue + ' ' + res.tenKhachThue)
          this.snackbar = false
        }).catch( res => {
          toast.Error('C li xy ra: ' + res)
        })
      } else {
        toast.Error('Vui lng chn phng cn xa!')
      }
    },
    DeleteMulti () {
      if (this.selected && this.selected.length != 0)
      {
        let deleteMultiId = this.selected.map(item => item._id)
        this.$store.dispatch('deleteMultiKhachThue', deleteMultiId).then(() => {
          toast.Success('Xa thnh cng!')
          this.snackbarMulti = false
          this.disabled = true
        })
      } else {
        toast.Show('Vui lng chn phng  xa')
      }
    },
    OpenDeleteMulti () {
      this.snackbarMulti = true
    },
    ResetSnackback () {
      this.idXoa = '',
      this.tenKhachThue = '',
      this.snackbar = false
    },
    ResetSnackbackMulti () {
      this.snackbarMulti = false
    }
  },
  filters: {
    formarDate (date) {
      return moment(date).format('DD/MM/YYYY')
    }
  }
}