import DatePicker from 'vue2-datepicker'
import ThongKeServices from '../services.js'
import toast from '../../../plugins/toast'
import moment from 'moment'
import downloadExcel from 'vue-json-excel'
export default {
  components: { DatePicker, downloadExcel },
  data() {
    return {
      formData: {},
      search: '',
      nameExcel:'',
      pagination: {
        rowsPerPage:5
      },
      loadingTK:false,
      lang: {
        days: ['CN', 'T.2', 'T.3', 'T.4', 'T.5', 'T.6', 'T.7'],
        months: ['Thng 1', 'Thng 2', 'Thng 3', 'Thng 4', 'Thng 5', 'Thng 6', 'Thng 7', 'Thng 8', 'Thng 9', 'Thng 10', 'Thng 11', 'Thng 12'],
        pickers: ['ti 7 ngy', 'ti 1 thng', 'lui 7 ngy', 'lui 1 thng'],
        placeholder: {
          date: 'Select Date',
          dateRange: 'Chn ngy thng k'
        }
      },
      headers: [
        { text: 'M S Phiu Thu',value: '_id' },
        { text: 'Tn Phng', value: 'phongID.tenPhong' },
        { text: 'Ngy Lp', value: 'ngayLap' },
        { text: 'Ngy Kt Thc', value: 'ngayHetHan' }
      ],
      exportHeader1: {
        'M Phiu Thu': '_id',
        'Phng Thu': 'phongID.tenPhong',
        'Khu Phng': 'phongID.khuPhongID.tenKhuPhong',
        'Ngy Lp': {
          field: 'ngayLap',
          callback: (value) => {
            return  moment(value).format('DD/MM/YYYY')
          }
        },
        'Ngy Ht Hn': {
          field: 'ngayHetHan',
          callback: (value) => {
            return  moment(value).format('DD/MM/YYYY')
          }
        } 
      },
      dsPT: []
    }
  },
  computed: {
    pages () {
      if (this.pagination.rowsPerPage == null ||
        this.dsPT.length  === 0
      ) return 0
      return Math.ceil(this.dsPT.length / this.pagination.rowsPerPage)
    }
  },
  methods: {
   async ThongKePhieuThu () {
      try {
        this.loadingTK = true
        this.dsPT = await ThongKeServices.ThongKePhieuThu(this.formData)
        let date = new Date()
        this.nameExcel = `phieu-thu-tien-${date.getMonth() +1 +'-' +date.getFullYear()}.xls`
        if(this.dsPT.length > 0)
        {
          this.loadingTK = false
          toast.Success(`C ${this.dsPT.length} ${this.formData.tieuChi === 'ptLap'? 'Phiu Thu Lp':'Phiu Thu Ht Hn'} c tm thy!`)
        } else {
          this.loadingTK = false
          toast.Info(`Khng c  ${this.formData.tieuChi === 'ptLap'? 'Phiu Thu Lp':'Phiu Thu Ht Hn'} no c tm thy!`)
        }
      } catch (err) {
        toast.Error(err)
      }
    },
    async BaoHetHanPT () {
      try {
        let res = await ThongKeServices.BaoHetHanPT(this.dsPT)
        if(res) {
          toast.Success(' gi mail bo ht hn phiu thu!')
        }
      } catch (err) {
        toast.Error(err)
      }
    }
  },
  filters: {
    formarDate (date) {
      return moment(date).format('DD/MM/YYYY')
    }
  }
}