const required = v => !!v || 'Khng c  trng'
const email = v => /.+@.+/.test(v) || 'Khng ng nh dng mail'

const max20 = v =>  {
 return  (v && v.length <= 20) ||  ` ti a 20 k t`
}
const max10 = v =>  {
 return  (v && v.length <= 10) ||  ` ti a 10 k t`
}
const max13 = v =>  {
 return  (v && v.length <= 13) ||  ` ti a 13 k t`
}
const max30 = v =>  {
 return  (v && v.length <= 30) ||  ` ti a 30 k t`
}
const max40 = v =>  {
 return  (v && v.length <= 40) ||  ` ti a 40 k t`
}


export default {
  required,
  email,
  max10,
  max20,
  max13,
  max30,
  max40,
}