<template>
  <v-img id="inspire"  style="height:100vh;" :lazy-src="require('@/assets/loginLazy.jpg')" :src="require('@/assets/login.jpg')"  class="grey lighten-2 loginForm">
   <template v-slot:placeholder>
      <v-layout fill-height align-center justify-center ma-0 >
        <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
      </v-layout>
    </template>
    <div class="loginbox">
      <h1 style="text-align:center">Phng Tr C Mai</h1>
      <hr>
      <v-flex style="display:flex" justify-center>
        <div class="pt-4" style="width:400px;">
          <v-text-field append-icon="people" v-model="username"  type="text" hide-details background-color="white" dark label="Tn ng Nhp" outline ></v-text-field>
        </div>
      </v-flex>
      <v-flex style="display:flex" justify-center>
        <div class="pt-4" style="width:400px;">
          <v-text-field  append-icon="lock" v-model="password" @keyup.enter="Login" type="passWord" hide-details background-color="white" dark label="Mt Khu" outline ></v-text-field>
        </div>
      </v-flex>
      <v-flex style="display:flex" justify-center>
        <div class="pt-4" style="width:300px;text-align: center;">
          <v-btn color="cyan" :loading="loading" @click="Login" dark>ng Nhp</v-btn>
        </div>
      </v-flex>
      <v-flex style="display:flex" justify-end>
        <div class="pt-4" style="width:300px;text-align: center;">
          <router-link style="text-decoration:none;" class="pt-2 white--text" :to="{path:'/index.html'}">v trang ch</router-link>
        </div>
        <div class="pt-4" style="width:300px;text-align: center;">
          <a href="#" @click="openForget = true" class="textlink">Qun mt khu?</a>        
        </div>
      </v-flex>
      <forgetpassword v-model="openForget" />
    </div>
  </v-img>
</template>

<script>
  import toast from '../plugins/toast.js'
  import forgetpassword from '../modules/User/components/ForgetPassword.vue'
  export default {
    components: {
      forgetpassword
    },
    data: () => ({
      drawer: null,
      username: '',
      password: '',
      loading: false,
      openForget: false
    }),
    props: {
      source: String
    },
    methods: {
      Login () {
        this.loading = true
        let payload = {userName: this.username, passWord: this.password}
        this.$store.dispatch('auth/login', payload).then(res => { 
          /*chng ta check role ca account  nu thuc din account admin th vo admin,
            khch th vo khch,
            cn cc nhm cn li th chuyn sang route admin (route admin l route mc nh)
          */
          if(res.isValid === true && res.credentials.status === false) {
            toast.Error('Vui lng vo mail  kch hot ti khon')
            this.loading = false
          }
          else if(res.isValid === true)
          {
            toast.Success(`Xin cho ${res.credentials.rolesGroupID.nameRoleGroup}: ${res.credentials.userInfo.tenNhanVien || res.credentials.userInfo.tenKhachThue}`)
            if(res.credentials.rolesGroupID._id === '5cc565ac9f49904f20b6211e') {
              this.$router.push({path: '/tong-quan.html'})
            }
            else if(res.credentials.rolesGroupID._id === '5cc565b39f49904f20b6211f') {
              this.$router.push({path: '/thong-tin-phong-dang-thue.html'})
            }
            else {
              this.$router.push({path: '/admin'})
            }
            this.loading = false
          }
          else if (res.credentials === null)
          {
            toast.Error(`Ti khon ${this.username} khng tn ti`)
             this.loading = false
          }
          else {
            toast.Error(`Mt khu khng ng!`)
            this.loading = false
          }
        }).catch (error => {
          alert(error + ' Li')
        })
      }
    }
  }
</script>
<style scoped>
  .loginbox {
    position: absolute;
    width: 589px;
    height: 350px;
    top:50%;
    left: 50%;
    background: rgba(0,0,0,.8);
    color: white;
    transform: translate(-50%,-50%);
    border-radius: 12px;
  }
  .textlink {
    color: white;
    text-decoration: none;
  }
</style>