import DatePicker from 'vue2-datepicker'
import BookPhongServices from './BookPhongServices.js'
import toast from '../../../plugins/toast.js'
import validate from '../../../plugins/validation.js'
export default {
  components: { 
    DatePicker
  },
  props:{
    phongSelected: {
      type: Object
    },
    value: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      valid: true,
      hoNguoiBookRules : [
        validate.required,
        validate.max20
      ],
      tenNguoiBookRules: [
        validate.required,
        validate.max13
      ],
      emailRules: [
        validate.required,
        validate.email
      ],
      soCMNDRules: [
        validate.required,
        validate.max20
      ],
      soDienThoaiRules: [
        validate.required,
        validate.max13
      ],
      diaChiRules: [
        validate.required,
        validate.max30
      ],
      ngayNhanPhongRules: [
        validate.required,
        v => (console.log('value',v))
      ],
      formData: {},
      loadingBook: false,
      lang: {
        days: ['CN', 'T.2', 'T.3', 'T.4', 'T.5', 'T.6', 'T.7'],
        months: ['Thng 1', 'Thng 2', 'Thng 3', 'Thng 4', 'Thng 5', 'Thng 6', 'Thng 7', 'Thng 8', 'Thng 9', 'Thng 10', 'Thng 11', 'Thng 12'],
        pickers: ['ti 7 ngy', 'ti 1 thng', 'lui 7 ngy', 'lui 1 thng'],
        placeholder: {
          date: 'Chn ngy nhn phng'
        }
      }
    }
  },
  methods: {   
    Book () {
      if (this.$refs.form.validate() && this.formData.ngayNhanPhong) {
        this.loadingBook = true
        BookPhongServices.bookPhong(this.formData).then(() => {
          this.loadingBook = false
          toast.Success('Vui lng check mail  kch hot')
        }).catch(err => {
          toast.Error('Li',err)
        })
    }
    },
    Reset () {
      this.$refs.form.reset()
      this.formData = {}
      this.formData.phongID = this.phongSelected._id
      this.formData.ngayBookPhong = new Date()
      this.formData.status = false
    }
  },
  watch: {
    value (v) {
      if(v) {
        this.formData.phongID = this.phongSelected._id
        this.formData.ngayBookPhong = new Date()
        this.formData.status = false
      } else {
        this.Reset()
      }
    }
  },
}